@import url(https://fonts.googleapis.com/css2?family=Montserrat+Alternates:wght@400;600;700&display=swap);
/* Default Font Styles
______________________*/
body, input, select, textarea {
  font-family: 'Montserrat Alternates', sans-serif;
  font-size: 14px;
  color: #ABA7D5;
  line-height: 1.3;
}
.body{
  background: #0B0516;
  min-height: 100vh;
}


/* Headings
______________________*/
h1,h2,h3,h4,h5,h6{
  font-weight: bold;
  margin: 0 0 20px;
}


/* Links
______________________*/

@media (min-width: 1600px) {
  body .container{
    max-width: 1600px;
  }
}

img {
  max-width: 100%;
  height: auto;
}

a {
  color: #7C7C7C !important;
}

a:hover {
  color: #7C7C7C;
  text-decoration: none;
}
.text-underline{
  text-decoration: underline;
}


/* p, blockquote, address
______________________*/
p {
  line-height: 1.6;
  margin: 0 0 20px;
}

.banner-outer{
  padding: 45px 0 0;
  background: #0b0516;
  min-height: calc(100vh - 105px);
  position: relative;
  overflow: hidden;
}
  .banner-outer:before{
    content: " ";
    width: 600px;
    height: 600px;
    background: rgba(255,255,255, 0.6);
    opacity: 6%;
    position: absolute;
    left: -100px;
    bottom: -200px;
    border-radius: 50%;
  }
  .banner-outer:after{
    content: " ";
    width: 550px;
    height: 550px;
    background: rgba(255,255,255, 0.6);
    opacity: 6%;
    position: absolute;
    right: -100px;
    bottom: -200px;
    border-radius: 50%;
  }
.banner-bot{
  height: 100px;
  background:#0B0516;
}
.banner-section{
  background: url(/static/media/banner.8fe2c3e7.png) center top no-repeat;
  background-size: 100% auto;
  min-height: 690px;
  max-width: 1500px;
  margin: 0 auto;
  position: relative;
}
.banner-section:before{
  content: " ";
  width: 400px;
  height: 400px;
  background: rgba(255,255,255, 0.6);
  opacity: 6%;
  position: absolute;
  left: -92px;
  top: -177px;
  border-radius: 50%;
}
.banner-content{
  padding: 85px 0 0 60px;
}
.logo{
  position: relative;
  margin: 0 0 40px;
}
.logo img{
  width: 180px;
}
.banner-content h1{
  color: #fff;
  font-weight: bold;
  font-size: 80px;
  line-height: 1;
  margin: 0 0 10px;
}
.banner-icons{
  position: absolute;
  left: 60px;
  bottom: 55px;
}
.banner-icons ul,
.banner-icons li{
  list-style: none;
  margin: 0;
  padding: 0;
}
.banner-icons ul{
  display: flex;
  flex-wrap: wrap;
}
.banner-icons li{
  margin: 0 15px 15px 0;
}
.banner-icons li img{
  max-width: 160px;
}
.friends{
  display: block;
  margin: 0 0 30px;
}

/* footer
______________________*/
#footer{
  padding: 25px 0;
}
    #footer ul,
    #footer li{
        list-style: none;
        margin: 0;
        padding: 0;
    }
    .footer-section-wrap{
        display: flex;
        flex-wrap: wrap;
        align-items: center;
    }
    .footer-logo{
        width: 150px;
    }
    .footer-nav{
        width: calc(100% - 200px);
        margin-left: auto;
    }
    #footer .footer-nav ul{
      display: flex;
      flex-wrap: wrap;
    }
    #footer .footer-nav li{
      margin: 5px 20px 5px 0;
    }
  #footer .footer-icons ul{
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
  }
  #footer .footer-icons ul li{
    margin: 0 10px 10px 0;
  }
  #footer .footer-icons li img{
  }


.cl {
  clear: both;
  font-size: 0;
  height: 0;
}

.clearfix:after {
  clear: both;
  content: ' ';
  display: block;
  font-size: 0;
  line-height: 0;
  visibility: hidden;
  width: 0;
  height: 0;
}

@media (max-width: 1600px) {
  .banner-section{
    min-height: 510px;
  }
  .banner-section:before{
    width: 300px;
    height: 300px;
    left: -72px;
    top: -133px;
  }
  .banner-content{
    padding: 85px 0 0 40px;
  }
  .logo img{
    width: 150px;
  }
  .banner-content h1{
    font-size: 60px;
  }
  .friends img{
    width: 200px;
  }
  .banner-icons{
    bottom: 35px;
    left: 40px;
  }
  .banner-icons li img{
    width: 120px;
  }
}
@media (max-width: 1200px) {
  .banner-outer:before{
    width: 550px;
    height: 550px;
  }
  .banner-outer:after{
    width: 500px;
    height: 500px;
  }
  .banner-section{
    min-height: 440px;
  }
  .banner-section:before{
    width: 250px;
    height: 250px;
    left: -57px;
    top: -110px;
  }
  .banner-content{
    padding: 55px 0 0 30px;
  }
  .logo img{
    width: 120px;
  }
  .banner-content h1{
    font-size: 50px;
  }
  .friends img{
    width: 150px;
  }
  .banner-icons{
    bottom: 40px;
    left: 30px;
  }
  .banner-icons li img{
    width: 100px;
  }
  .footer-logo{
    width: 120px;
  }
  .footer-nav {
    width: calc(100% - 170px);
  }
}
@media (max-width: 991px) {
  .banner-outer:before{
    width: 450px;
    height: 450px;
  }
  .banner-outer:after{
    width: 400px;
    height: 400px;
  }
  .banner-section{
    min-height: 335px;
  }
  .banner-section:before{
    width: 200px;
    height: 200px;
    left: -56px;
    top: -84px;
  }
  .banner-content{
    padding: 35px 0 0 20px;
  }
  .logo{
    margin: 0 0 20px;
  }
  .logo img{
    width: 80px;
  }
  .banner-content h1{
    font-size: 36px;
  }
  .friends img{
    width: 100px;
  }
  .banner-icons{
    bottom: 40px;
    left: 20px;
  }
  .banner-icons li img{
    width: 75px;
  }
  #footer{
    font-size: 13px;
  }
  .footer-logo{
    width: 100px;
  }
  .footer-nav {
    width: calc(100% - 130px);
  }
}
@media (max-width: 767px) {
  .banner-section{
    min-height: 275px;
    max-width: 375px;
    margin: 0 auto;
  }
  .banner-section:before{
    display: none;
  }
  .banner-outer:before{
    width: 200px;
    height: 200px;
    left: -50px;
    bottom: -50px;
  }
  .banner-outer:after{
    width: 200px;
    height: 200px;
    right: -50px;
    bottom: -50px;
  }
  .logo{
    margin: 0 0 10px;
  }
  .logo img{
    width: 60px;
  }
  .banner-content h1{
    font-size: 22px;
    margin: 0 0 5px;
  }
  .friends img{
    width: 70px;
  }
  .banner-icons{
    bottom: 40px;
    left: 20px;
  }
  .banner-icons li img{
    width: 75px;
  }
  .banner-bot{
    display: none;
  }
  #footer{
    padding: 25px 15px;
  }
  .footer-logo{
    width: 80px;
    margin: 0 0 5px;
  }
  .footer-nav {
    width: 100%;
  }
  #footer .footer-icons ul{
    justify-content: flex-start;
    padding: 15px 0 0;
  }
}
@media (max-width: 330px) {
  .banner-content{
    padding: 10px 0 0 10px;
  }
}
